.page-wrapper {
	overflow : hidden;
}

.container {
	box-sizing : border-box;
	width      : (1096 + 20) * 1px;
	max-width  : 100%;
	margin     : auto;
	padding    : 0 10px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}


.list-reset {
	padding    : 0;
	list-style : none;
	margin     : 0;
}

.scroll {
	overflow : hidden;
	position : fixed;
	width    : 100%;
}

.hero {
	height                : 100dvh;
	background-image      : url("../images/hero/hero-bg-mob.jpg");
	background-size       : cover;
	background-repeat     : no-repeat;
	background-position   : center center;
	display               : flex;
	flex-direction        : column;
	justify-content       : center;
	background-attachment : fixed;
	@media screen and (min-width : $break-table) {
		background-image      : url("../images/hero/hero-bg.jpg");
	}
	@media screen and (min-width : $break-desktop) {

	}
}


.bonus {
	padding : 30px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 64px 0 84px 0;
	}
}

.bonus__container {
	max-width : 1287px;
	margin    : 0 auto;
	padding   : 0 7px;
}

.bonus-text {
	background-image  : url("../images/bonus/bonus-bg.jpg");
	background-repeat : no-repeat;
	background-size   : cover;
	border            : 1px solid #63005F;
	border-radius     : 10px;
	padding           : 10px 10px;
	margin            : 0 auto 30px auto;
	@media screen and (min-width : $break-desktop) {
		margin  : 0 auto 64px auto;
		padding : 20px 20px;
	}
}

.bonus-text__title {
	font-weight    : 700;
	font-size      : 20px;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;
	margin-bottom  : 0;
	@media screen and (min-width : $break-desktop) {
		font-size : 48px;
	}
}

.bonus-text__txt {
	font-weight    : 400;
	font-size      : 14px;
	line-height    : 143%;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 143%;
	}
}

.bonus-item {
	background            : linear-gradient(0deg, #250124 0%, #63005F 100%);
	border-radius         : 10px;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	align-items           : center;
	grid-row-gap          : 15px;
	padding               : 0 20px 10px 20px;
	grid-column-gap       : 15px;
	overflow              : hidden;
	box-shadow            : 0 0 4px #5D0002;
	max-width             : 1100px;
	margin                : 0 auto;

	&:not(:last-child) {
		margin-bottom : 30px;
	}

	@media screen and (min-width : 906px) {

		grid-template-columns : repeat(12, 1fr);
		grid-row-gap          : 0;
		padding               : 30px 30px;
		grid-column-gap       : 0;
		&:not(:last-child) {
			margin-bottom : 40px;
		}
	}
}

.bonus-item__wrap {

	&:nth-child(1) {
		grid-column : 1/5;

		@media screen and (min-width : 906px) {
			grid-column : 1/4;
		}
	}

	&:nth-child(2) {
		grid-column : 4 span;

		@media screen and (min-width : 906px) {
			grid-column : 4/8;

		}
	}

	&:nth-child(3) {
		grid-column : 1/2;

		@media screen and (min-width : 906px) {
			grid-column : 8/10;

		}
	}

	&:nth-child(4) {
		grid-column     : 2/5;
		display         : flex;
		justify-content : flex-end;

		.b-btn--primary {
			min-width : 195px;


			@media screen and (min-width : 906px) {
				min-width     : 170px;
				margin-bottom : 8px;

			}
		}

		@media screen and (min-width : 906px) {
			grid-column     : 10/13;
			flex-direction  : column;
			justify-content : unset;
			align-items     : center;
		}
	}
}

.bonus-item__logo {

	display         : flex;
	align-items     : center;
	justify-content : center;


	@media screen and (min-width : 906px) {

		max-width : none;
		margin    : 0;
	}

	img {
		height : 70px;
		width  : 250px;
		@media screen and (min-width : 906px) {
			width  : 229px;
			height : 138px;
		}
	}
}

.bonus-item__text {
	font-weight    : 700;
	font-size      : 14px;
	line-height    : 143%;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;
	margin-right   : auto;
	text-shadow    : 0 4px 4px rgba(0, 0, 0, 0.25);
	margin-top     : auto;
	@media screen and (min-width : 906px) {
		font-size   : 20px;
		line-height : 125%;

	}
}


.bonus-rating {
	display        : flex;
	flex-direction : column;
	align-items    : center;
	margin-top     : auto;
}

.bonus-rating__value {
	font-weight : 700;
	font-size   : 24px;
	line-height : 217%;
	text-align  : center;
	color       : #FFF;

	@media screen and (min-width : 906px) {
		font-size   : 40px;
		line-height : 130%;

	}
}

.bonus-rating__text {
	font-weight : 400;
	font-size   : 16px;
	line-height : 156%;
	text-align  : center;
	color       : #FFF;
	display     : none;
	@media screen and (min-width : 906px) {
		display : block;
	}
}

.bonus-rating__list {
	display     : flex;
	align-items : center;
	gap         : 2px;

	li {
		svg {
			width  : 17px;
			height : 18px;
			@media screen and (min-width : 906px) {
				width  : 25px;
				height : 25px;
			}
		}
	}
}

.about {
	background : #282828;
	padding    : 20px 0;
	@media screen and (min-width : $break-desktop) {

	}
}

.about__title {
	font-weight    : 700;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;
	font-size      : 20px;
	margin-bottom  : 18px;
	@media screen and (min-width : $break-desktop) {
		font-size : 32px;
	}
}

.about__wrapper {
	display        : flex;
	align-items    : center;
	flex-direction : column;
	text-align     : center;
}

.about__subtitle {
	font-weight    : 400;
	font-size      : 20px;
	line-height    : 125%;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;
	margin-bottom  : 16px;
}

.about__grid {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	grid-gap              : 18px 25px;
	margin-bottom         : 18px;
}

.about-item {
	grid-column : 2 span;

	&:last-child {
		grid-column : 4 span;

		.about-item__text {
			text-transform : unset;
			font-weight    : 700;
		}
	}
}

.about-item__img {
	margin-bottom : 10px;
}

.about-item__text {
	font-weight    : 400;
	font-size      : 16px;
	line-height    : 156%;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;
}

.about__subtitle--small {
	font-size     : 16px;
	line-height   : 156%;
	margin-bottom : 18px;
}

.about__subtitle--bold {
	font-weight   : 700;
	font-size     : 16px;
	line-height   : 156%;
	margin-bottom : 0;
}

.alert {
	background : #111;
	padding    : 20px 0;

	@media screen and (min-width : $break-desktop) {

	}
}

.alert__container {
	max-width : 1440px;
	padding   : 0 20px;
	margin    : 0 auto;
}

.alert__title {
	font-weight    : 700;
	font-size      : 20px;
	text-transform : uppercase;
	text-align     : center;
	color          : #FFF;
	margin-bottom  : 18px;

	@media screen and (min-width : $break-desktop) {
		font-size : 24px;
	}
}

.about__text {
	text-align    : center;
	font-weight   : 400;
	font-size     : 16px;
	line-height   : 125%;
	color         : #FFF;
	margin-bottom : 18px;

	@media screen and (min-width : $break-desktop) {
		font-size : 20px;
	}
}

.alert__list {
	display        : flex;
	align-items    : center;
	flex-direction : column;
	gap            : 10px;
	list-style     : none;
	padding        : 0;
	margin         : 0;

	@media screen and (min-width : $break-desktop) {
		flex-direction  : row;
		justify-content : space-between;
	}

	li {
		a {
			display : flex;
		}
	}
}

.rules {
	padding: 50px 0;

	@media screen and (min-width : $break-desktop) {
		padding: 90px 0;
	}

}

.rules__container {
	max-width: 1140px;
	margin: 0 auto;
	padding: 0 20px;
}

.rules__title {
	font-weight: 700;
	font-size: 20px;
	text-transform: uppercase;
	text-align: center;
	color: #fff;
	margin-bottom: 23px;

	@media screen and (min-width : $break-desktop) {
		font-size: 24px;
	}
}

.rules__text {
	font-weight: 400;
	font-size: 14px;
	@media screen and (min-width : $break-desktop) {
		font-size: 18px;
	}
	color: #fff;
	&:not(:last-child) {
		margin-bottom: 15px;

		@media screen and (min-width : $break-desktop) {
			margin-bottom: 25px;
		}
	}
}

.rules__subtitle {
	font-weight: 700;
	font-size: 14px;
	color: #fff;
	margin-bottom: 15px;

	@media screen and (min-width : $break-desktop) {
		font-size: 18px;
		margin-bottom: 25px;
	}
}
