.footer {
	background : #080808;
	padding    : 20px 0;

}

.footer__container {
	display        : flex;
	flex-direction : column;
	align-items    : center;
}

.footer__text {
	font-weight   : 400;
	font-size     : 16px;
	line-height   : 125%;
	text-align    : center;
	color         : #FFF;
	margin-bottom : 15px;
	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 125%;
		margin-bottom : 30px;
	}

}

.footer__link {
	font-weight     : 700;
	font-size       : 16px;
	line-height     : 125%;
	text-align      : center;
	color           : #FFF;
	display         : flex;
	text-decoration : none;

	@media screen and (min-width : $break-desktop) {
		font-size : 20px;
	}

	&:hover {
		color : #FFF;
	}
}
