.header {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	padding: 14px 15px 0 15px;
	z-index: 10;
	@media screen and (min-width : $break-desktop) {
		padding: 14px 35px 0 35px;
	}
}

.header__logo {
	flex-shrink: 0;
	width: 35px;

	@media screen and (min-width : $break-desktop) {
		width: 39px;
	}
}

.header__container {
	display: flex;
	align-items: center;
	justify-content: space-between;
	@media screen and (min-width : $break-desktop) {
		justify-content: unset;
		align-items: center;
	}
}

.nav {
	display         : flex;
	height          : 100dvh;
	align-items     : stretch;
	flex-flow       : column nowrap;
	justify-content : flex-start;
	box-sizing      : border-box;
	position        : fixed;
	right           : 0;
	top             : 0;
	transition      : transform .3s linear;
	transform       : translateX(100%);
	width           : 100%;
	z-index         : 101;
	overflow-y      : overlay;
	overflow-x      : hidden;
	padding         : 12px 12px;
	border-radius   : 0;
	border          : 1px solid rgba(255, 255, 255, .05);
	background      : rgba(0, 0, 0, 0.7);

	&.active {
		transform : translateX(0px);
		right     : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin: 0 auto;
		display: block;
		height: auto;
		overflow: visible;
		background: rgba(29, 29, 29, 0.8);
		transform: unset;
		width: auto;
		border: none;
		padding: 10px 30px;
		position: unset;
		border-radius: 10px;
	}

}

.nav__list {
	padding-top: 50px;
	display: flex;
	flex-direction: column;
	align-items: center;
	@media screen and (min-width : $break-desktop) {
		gap: 30px;
		flex-direction: row;
		align-items: center;
		padding-top: 0;

	}
}

.nav__item {

	&:not(:last-child) {
		margin-bottom: 12px;
		@media screen and (min-width : $break-desktop) {
			margin-bottom: 0;
		}
	}
}

.nav__link {
	border-radius: 20px;
	text-decoration: none;
	font-weight: 700;
	font-size: 12px;
	color: #fff;
	@media screen and (min-width : $break-desktop) {

	}
}

.burger {
	background-color : transparent;
	border           : none;
	color            : #FFF;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	padding          : 0;
	position         : relative;
	width            : 24px;
	z-index: 200;
	@media screen and (min-width : $break-desktop) {
		display: none;
	}
	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}
