@keyframes dropdown-display {
  0% {
    opacity: 0;
    transform: scale(0.98) translateY(-0.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
@keyframes dropdown-display-up {
  0% {
    opacity: 0;
    transform: scale(0.98) translateY(0.6em);
  }
  100% {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}
blockquote {
