    margin: 28px 0;
  }
}
@media screen and (min-width: 1200px) {
  blockquote {
    margin: 48px 0;
  }
}
blockquote:after {
  content: "";
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 0;
  width: 3px;
  background: #000;
}

.b-btn {
  line-height: 1.25;
  position: relative;
  display: inline-flex;
  align-items: center;
  flex-flow: row nowrap;
  justify-content: center;
  box-sizing: border-box;
  min-height: 49px;
  padding: 6px 20px;
  user-select: none;
  transition: background 0.3s ease, color 0.3s ease, border-color 0.3s ease;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 20px;
  font-weight: 700;
  font-size: 24px;
  color: #fff;
  text-transform: uppercase;
  gap: 10px;
}
.b-btn:hover, .b-btn:focus-within, .b-btn.focus {
  outline: 0;
}
.b-btn.disabled, .b-btn:disabled {
  cursor: default;
  opacity: 0.45;
  background: inherit;
  box-shadow: none;
}
.b-btn > svg {
  display: block;
  flex: 0 0 auto;
}
.b-btn--big {
  font-size: 18px;
  min-height: 59px;
  padding: 4px 30px;
}
.b-btn--small {
  font-size: 15px;
  min-height: 39px;
  padding: 4px 16px;
}
.b-btn--primary {
  border-radius: 10px;
  padding: 10px 30px;
  width: 192px;
  height: 45px;
  font-weight: 700;
  font-size: 14px;
  line-height: 179%;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background: linear-gradient(180deg, #20b100 0%, #115e00 100%);
}
@media screen and (min-width: 1200px) {
  .b-btn--primary {
    font-size: 20px;
    line-height: 125%;
  }
}
.b-btn--primary svg {
  margin: 0;
  height: 21px;
}
.b-btn--primary:hover, .b-btn--primary:focus-within, .b-btn--primary:active, .b-btn--primary.active {
  box-shadow: 0 0 10px 0 #fbff00;
  background: linear-gradient(180deg, #ffa200 0%, #a36800 100%);
  color: #fff;
}
.b-btn--loading:before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 10px;
  margin-left: -15px;
  transition: border 0.3s ease;
  animation: loadingSpin 1s infinite linear;
  vertical-align: middle;
  border: 3px solid rgba(255, 255, 255, 0.6);
  border-top-color: #FFF;
  border-radius: 50%;
}
.b-btn--loading.outline:before {
  border: 3px solid rgba(0, 0, 0, 0.2);
  border-top-color: #000;
}
.b-btn--loading.outline:hover:before {
  border: 3px solid rgba(255, 255, 255, 0.6);
  border-top-color: #FFF;
}
.b-btn--full-size {
  display: block;
  width: 100%;
}
.b-btn--full-size + .full-size {
  margin-right: 5px;
}
.b-btn + .b-btn {
  margin-left: 5px;
}

.b-btn-grouping {
  display: inline-flex;
}
.b-btn-grouping .b-btn {
  margin-right: 0;
  border-radius: 0;
}
.b-btn-grouping .b-btn:first-child {
  border-radius: 6pc 0 0 6pc;
}
.b-btn-grouping .b-btn:last-child {
  border-radius: 0 6pc 6pc 0;
}
.b-btn-grouping .b-btn.outline {
  z-index: 1;
  margin-left: -1px;
}
.b-btn-grouping .b-btn.outline:hover {
  z-index: 2;
}
.b-btn-grouping .b-btn.outline.active {
  z-index: 3;
}
.b-btn-grouping .b-btn.outline:first-child {
  margin-left: 0;
}

@keyframes loadingSpin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
  pointer-events: none;
}

input[type=submit].full-size,
input[type=reset].full-size,
input[type=button].full-size {
  width: 100%;
}

