}
@media screen and (min-width: 1200px) {
  .header__container {
    justify-content: unset;
    align-items: center;
  }
}

.nav {
  display: flex;
  height: 100dvh;
  align-items: stretch;
  flex-flow: column nowrap;
  justify-content: flex-start;
  box-sizing: border-box;
  position: fixed;
  right: 0;
  top: 0;
  transition: transform 0.3s linear;
  transform: translateX(100%);
  width: 100%;
  z-index: 101;
  overflow-y: overlay;
  overflow-x: hidden;
  padding: 12px 12px;
  border-radius: 0;
  border: 1px solid rgba(255, 255, 255, 0.05);
  background: rgba(0, 0, 0, 0.7);
}
.nav.active {
  transform: translateX(0px);
  right: 0;
}
@media screen and (min-width: 1200px) {
  .nav {
    margin: 0 auto;
    display: block;
    height: auto;
    overflow: visible;
    background: rgba(29, 29, 29, 0.8);
    transform: unset;
    width: auto;
    border: none;
    padding: 10px 30px;
    position: unset;
    border-radius: 10px;
  }
}

.nav__list {
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 1200px) {
  .nav__list {
    gap: 30px;
    flex-direction: row;
    align-items: center;
    padding-top: 0;
  }
}

.nav__item:not(:last-child) {
  margin-bottom: 12px;
}
@media screen and (min-width: 1200px) {
  .nav__item:not(:last-child) {
    margin-bottom: 0;
  }
}

.nav__link {
  border-radius: 20px;
  text-decoration: none;
  font-weight: 700;
  font-size: 12px;
  color: #fff;
}
.burger {
  background-color: transparent;
  border: none;
  color: #FFF;
  cursor: pointer;
  flex-shrink: 0;
  height: 16px;
  padding: 0;
  position: relative;
  width: 24px;
  z-index: 200;
}
@media screen and (min-width: 1200px) {
  .burger {
    display: none;
  }
}
.burger::before, .burger::after {
  background-color: currentColor;
  content: "";
  height: 2px;
  left: 0;
  position: absolute;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  width: 100%;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - 2px);
}
.burger__line {
  background-color: currentColor;
  border-radius: 10px;
  height: 2px;
  left: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
  width: 100%;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

